import { create } from "zustand";
import { persist, subscribeWithSelector } from "zustand/middleware";
import { produce } from "immer";
import { v4 as uuidv4 } from "uuid";
import { computeDailyMetrics } from "@/utils/computeDailyMetrics";
import { DailyMetrics, LoadingState } from "@/types/DataModels";
import { WorkoutLogEntry as OldWorkoutLogEntry } from "@/types/Workout_Log";
import { Exercise as ExerciseType } from "@/types/Exercises";
import {
  WorkoutTemplate as WorkoutTemplateType,
  ActiveWorkout,
} from "@/types/Workout_Templates";
import {
  readSpreadsheetData,
  createSpreadsheetRow,
  updateSpreadsheetRow,
  deleteSpreadsheetRow,
  GenericSheetRowData,
} from "@/lib/spreadsheetAPI";

// Custom immer middleware - simplified for compatibility
const immer =
  <T>(
    initializer: (
      set: (fn: (state: T) => void) => void,
      get: () => T,
      store: unknown,
    ) => T,
  ) =>
  (
    set: (state: T | Partial<T> | ((state: T) => T | Partial<T>)) => void,
    get: () => T,
    store: unknown,
  ) =>
    initializer(
      (fn: (state: T) => void) => set(produce(fn) as (state: T) => T),
      get,
      store,
    );

// Import app store for authentication state
import { useAppStore } from "./appStore";

// Helper function to check authentication status
const checkAuthenticationStatus = (): {
  isAuthenticated: boolean;
  hasToken: boolean;
  authError: string | null;
} => {
  try {
    const appState = useAppStore.getState();
    return {
      isAuthenticated: appState.isAuthenticated,
      hasToken: !!appState.token,
      authError: appState.authError,
    };
  } catch (error) {
    console.error("Error checking authentication status:", error);
    return {
      isAuthenticated: false,
      hasToken: false,
      authError: "Failed to check authentication status",
    };
  }
};

// Define type for workout analysis result
interface WorkoutAnalysis {
  date: string;
  totalVolume: number;
  maxWeight: number;
  totalReps: number;
  weight: number;
  reps: number;
  highestVolumeDate: string;
  totalSets: number;
  allSets: Array<{
    id: string;
    weight: number;
    reps: number;
    rating: number;
    restTime: number | null | undefined;
    volume: number;
  }>;
  highestVolume: {
    date: string;
    totalVolume: number;
    maxWeight: number;
    totalReps: number;
    weight: number;
    reps: number;
  };
  progressionTargets: {
    targetVolume: number;
    progressionRate: number;
    sameWeightMoreReps: {
      weight: number;
      reps: number;
      additionalReps: number;
      sets: number;
    };
    moreSets: {
      weight: number;
      reps: number;
      additionalSets: number;
      sets: number;
    };
    balancedProgression: {
      weight: number;
      reps: number;
      additionalReps: number;
      sets: number;
      additionalSets: number;
    };
  };
}

// Define Layout type (from react-grid-layout)
interface Layout {
  i: string;
  x: number;
  y: number;
  w: number;
  h: number;
  minW?: number;
  maxW?: number;
  minH?: number;
  maxH?: number;
  static?: boolean;
  isDraggable?: boolean;
  isResizable?: boolean;
}

// =============================================================================
// UNIFIED INTERFACES
// =============================================================================

interface ExerciseGroup {
  muscleGroup: string;
  exercises: string[];
}

interface ErrorState {
  exercises: string | null;
  workoutLogs: string | null;
  templates: string | null;
  general: string | null;
}

interface DataFetchedState {
  exercises: boolean;
  workoutLogs: boolean;
  templates: boolean;
}

// Extend LoadingState to include specific loading states
interface ExtendedLoadingState extends LoadingState {
  exercises: boolean;
  workoutLogs: boolean;
  templates: boolean;
  general: boolean;
}

// Default dashboard layouts
const defaultDashboardLayouts = {
  lg: [
    { i: "stats", x: 0, y: 0, w: 12, h: 3 },
    { i: "charts", x: 0, y: 3, w: 8, h: 6 },
    { i: "recent", x: 8, y: 3, w: 4, h: 6 },
    { i: "summary", x: 0, y: 9, w: 12, h: 5 },
  ],
  md: [
    { i: "stats", x: 0, y: 0, w: 10, h: 3 },
    { i: "charts", x: 0, y: 3, w: 6, h: 6 },
    { i: "recent", x: 6, y: 3, w: 4, h: 6 },
    { i: "summary", x: 0, y: 9, w: 10, h: 5 },
  ],
  sm: [
    { i: "stats", x: 0, y: 0, w: 6, h: 3 },
    { i: "charts", x: 0, y: 3, w: 6, h: 6 },
    { i: "recent", x: 0, y: 9, w: 6, h: 6 },
    { i: "summary", x: 0, y: 15, w: 6, h: 5 },
  ],
};

interface UnifiedDataState {
  // =============================================================================
  // DATA STATE
  // =============================================================================

  // Exercises
  exercises: ExerciseType[];
  exerciseGroups: ExerciseGroup[];

  // Workout Logs
  workoutLogs: OldWorkoutLogEntry[];
  dailyMetrics: DailyMetrics[];

  // Workout Templates
  templates: WorkoutTemplateType[];
  activeWorkout: ActiveWorkout | null;

  // Dashboard Layout
  dashboardLayouts: { [key: string]: Layout[] };
  isDashboardEditMode: boolean;
  isDashboardCustomizable: boolean;

  // =============================================================================
  // LOADING & ERROR STATE
  // =============================================================================

  loading: ExtendedLoadingState;
  errors: ErrorState;
  dataFetched: DataFetchedState;

  // =============================================================================
  // EXERCISE ACTIONS
  // =============================================================================

  fetchExercises: (forceRefresh?: boolean) => Promise<void>;
  getExercisesByMuscleGroup: (muscleGroup: string) => string[];
  getMuscleGroupsByExercise: (exercise: string) => string[];
  getAllUniqueExerciseNames: () => string[];

  // =============================================================================
  // WORKOUT LOG ACTIONS
  // =============================================================================

  fetchWorkoutLogs: (forceRefresh?: boolean) => Promise<void>;
  addWorkoutLog: (
    newLog: Omit<OldWorkoutLogEntry, "id">,
  ) => Promise<OldWorkoutLogEntry | null>;
  editWorkoutLog: (updatedLog: OldWorkoutLogEntry) => Promise<void>;
  removeWorkoutLog: (logId: string) => Promise<void>;
  syncPendingLogs: () => Promise<void>;
  getPreviousHighestVolumeWorkout: (
    exercise: string,
    muscleGroup: string,
  ) => WorkoutAnalysis | null;

  // Computed metrics
  computeDailyMetricsData: () => void;

  // Grouped actions for easy access
  workoutLogActions: {
    fetchAll: () => Promise<void>;
    create: (
      data: Omit<OldWorkoutLogEntry, "id">,
    ) => Promise<OldWorkoutLogEntry | null>;
    update: (updatedLog: OldWorkoutLogEntry) => Promise<void>;
    delete: (logId: string) => Promise<void>;
  };

  // =============================================================================
  // WORKOUT TEMPLATE ACTIONS
  // =============================================================================

  fetchTemplates: (forceRefresh?: boolean) => Promise<void>;
  saveTemplate: (
    template: WorkoutTemplateType,
  ) => Promise<{ success: boolean; error?: string }>;
  deleteTemplate: (
    templateId: string,
  ) => Promise<{ success: boolean; error?: string }>;
  getTemplateByName: (name: string) => WorkoutTemplateType | undefined;
  getTemplateById: (id: string) => WorkoutTemplateType | undefined;
  updateTemplate: (template: WorkoutTemplateType) => void;

  // Active workout functions
  startWorkout: (
    templateName: string,
  ) => Promise<{ success: boolean; error?: string }>;
  completeWorkout: () => Promise<{ success: boolean; error?: string }>;
  updateActiveWorkout: (workout: Partial<ActiveWorkout>) => void;
  updateExerciseProgress: (
    exerciseIndex: number,
    setsCompleted: number,
  ) => void;
  addExerciseSet: (exerciseIndex: number) => void;
  cancelWorkout: () => void;
  getActiveWorkout: () => ActiveWorkout | null;

  // =============================================================================
  // DASHBOARD LAYOUT ACTIONS
  // =============================================================================

  setDashboardLayouts: (layouts: { [key: string]: Layout[] }) => void;
  resetDashboardLayouts: () => void;
  toggleDashboardEditMode: () => void;
  setDashboardEditMode: (isEditMode: boolean) => void;
  setDashboardCustomizable: (customizable: boolean) => void;

  // =============================================================================
  // GENERAL ACTIONS
  // =============================================================================

  clearAllData: () => void;
  clearError: (errorKey: keyof ErrorState) => void;
  fetchAllData: (forceRefresh?: boolean) => Promise<void>;
  testDataFetch: () => Promise<void>;
}

// =============================================================================
// UTILITY FUNCTIONS
// =============================================================================

const generateId = () => uuidv4();

const clearApiCache = () => {
  if (navigator.serviceWorker && navigator.serviceWorker.controller) {
    navigator.serviceWorker.controller.postMessage({
      type: "CLEAR_API_CACHE",
      cacheName: "sheets-api-cache",
    });
    console.log(
      "[UnifiedDataStore] Sent message to SW to clear sheets-api-cache.",
    );
  }
};

// =============================================================================
// UNIFIED DATA STORE IMPLEMENTATION
// =============================================================================

export const unifiedDataStore = create<UnifiedDataState>()(
  subscribeWithSelector(
    persist(
      immer((set, get) => ({
        // =============================================================================
        // INITIAL STATE
        // =============================================================================

        // Data
        exercises: [],
        exerciseGroups: [],
        workoutLogs: [],
        dailyMetrics: [],
        templates: [],
        activeWorkout: null,
        dashboardLayouts: defaultDashboardLayouts,
        isDashboardEditMode: false,
        isDashboardCustomizable: true,

        // Loading state
        loading: {
          isLoading: false,
          exercises: false,
          workoutLogs: false,
          templates: false,
          general: false,
        },

        // Error state
        errors: {
          exercises: null,
          workoutLogs: null,
          templates: null,
          general: null,
        },

        // Data fetched state
        dataFetched: {
          exercises: false,
          workoutLogs: false,
          templates: false,
        },

        // =============================================================================
        // EXERCISE ACTIONS
        // =============================================================================
        fetchExercises: async (forceRefresh = false) => {
          // Check authentication status before attempting to fetch data
          const authStatus = checkAuthenticationStatus();

          if (!authStatus.isAuthenticated || !authStatus.hasToken) {
            const errorMessage =
              authStatus.authError ||
              "Cannot fetch exercises: User not authenticated or missing token";
            console.warn(
              "UnifiedDataStore: Skipping exercises fetch -",
              errorMessage,
            );

            set((state) => {
              state.errors.exercises = errorMessage;
              state.loading.exercises = false;
            });
            return;
          }

          const isOnline = typeof navigator !== "undefined" && navigator.onLine;
          if (!isOnline && !forceRefresh) {
            console.log("Offline, using cached exercises data.");
            set((state) => {
              state.loading.exercises = false;
              state.errors.exercises = null;
            });
            return;
          }

          if (forceRefresh) {
            console.log(
              "[UnifiedDataStore] Force refreshing exercises data...",
            );
            clearApiCache();
            localStorage.removeItem("unified-workout-storage");
          }

          set((state) => {
            state.loading.exercises = true;
            state.errors.exercises = null;
          });

          try {
            // Use direct spreadsheet API call
            const result = await readSpreadsheetData("Exercises");

            if (result.success && result.data) {
              // Process exercise data and build exercise groups - fix field mapping for actual sheet column names
              const exercisesData = result.data.map(
                (row: GenericSheetRowData) => ({
                  muscleGroup: (row.Muscle_Group ||
                    row["Muscle Group"] ||
                    row.muscleGroup ||
                    "") as string,
                  exercise: (row.Exercise || row.exercise || "") as string,
                  difficultyLevel: (row.Difficulty_Level ||
                    row["Difficulty Level"] ||
                    row.difficultyLevel ||
                    "") as string,
                  equipmentRequired: (row.Equipment_Required ||
                    row["Equipment Required"] ||
                    row.equipmentRequired ||
                    "") as string,
                  targetIntensity: (row.Target_Intensity ||
                    row["Target Intensity"] ||
                    row.targetIntensity ||
                    "") as string,
                  primaryMuscleGroup: (row.Primary_Muscle_Group ||
                    row["Primary Muscle Group"] ||
                    row.primaryMuscleGroup ||
                    "") as string,
                  secondaryMuscleGroup: (row.Secondary_Muscle_Group ||
                    row["Secondary Muscle Group"] ||
                    row.secondaryMuscleGroup ||
                    "") as string,
                  exerciseDuration: (row.Exercise_Duration ||
                    row["Exercise Duration"] ||
                    row.exerciseDuration ||
                    "") as string,
                  recoveryTime: (row.Recovery_Time ||
                    row["Recovery Time"] ||
                    row.recoveryTime ||
                    "") as string,
                  exerciseType: (row.Exercise_Type ||
                    row["Exercise Type"] ||
                    row.exerciseType ||
                    "") as string,
                  caloriesBurned: parseInt(
                    String(
                      row.Calories_Burned ||
                        row["Calories Burned"] ||
                        row.caloriesBurned ||
                        "0",
                    ),
                  ),
                  exerciseProgression: (row.Exercise_Progression ||
                    row["Exercise Progression"] ||
                    row.exerciseProgression ||
                    "") as string,
                  injuryRiskLevel: (row.Injury_Risk_Level ||
                    row["Injury Risk Level"] ||
                    row.injuryRiskLevel ||
                    "") as string,
                  exerciseLink: (row.Exercise_Link ||
                    row["Exercise Link"] ||
                    row.exerciseLink ||
                    "") as string,
                  imageLink: (row.Image_Link ||
                    row["Image Link"] ||
                    row.imageLink ||
                    "") as string,
                  relatedPath: (row.Related_Path ||
                    row["Related Path"] ||
                    row.relatedPath ||
                    "") as string,
                }),
              ) as ExerciseType[];

              const exerciseGroupsMap = new Map<string, Set<string>>();

              exercisesData.forEach((ex) => {
                if (!exerciseGroupsMap.has(ex.muscleGroup)) {
                  exerciseGroupsMap.set(ex.muscleGroup, new Set());
                }
                exerciseGroupsMap.get(ex.muscleGroup)!.add(ex.exercise);
              });

              const exerciseGroups: ExerciseGroup[] = Array.from(
                exerciseGroupsMap.entries(),
              ).map(([muscleGroup, exercises]) => ({
                muscleGroup,
                exercises: Array.from(exercises),
              }));

              set((state) => {
                state.exercises = exercisesData;
                state.exerciseGroups = exerciseGroups;
                state.loading.exercises = false;
                state.dataFetched.exercises = true;
                state.errors.exercises = null;
              });
            } else {
              set((state) => {
                state.errors.exercises =
                  result.error || "Failed to fetch exercises data.";
                state.loading.exercises = false;
              });
            }
          } catch (err) {
            console.error("Error fetching exercises:", err);
            set((state) => {
              state.errors.exercises = (err as Error).message;
              state.loading.exercises = false;
            });
          }
        },

        getExercisesByMuscleGroup: (muscleGroup: string) => {
          const group = get().exerciseGroups.find(
            (g) => g.muscleGroup.toLowerCase() === muscleGroup.toLowerCase(),
          );
          return group ? group.exercises : [];
        },

        getMuscleGroupsByExercise: (exercise: string) => {
          const muscleGroups = new Set<string>();
          get().exercises.forEach((ex) => {
            if (ex.exercise.toLowerCase() === exercise.toLowerCase()) {
              muscleGroups.add(ex.muscleGroup);
            }
          });
          return Array.from(muscleGroups);
        },

        getAllUniqueExerciseNames: () => {
          const names = new Set<string>();
          get().exercises.forEach((e) => names.add(e.exercise));
          return Array.from(names);
        },

        // =============================================================================
        // WORKOUT LOG ACTIONS
        // =============================================================================
        fetchWorkoutLogs: async (forceRefresh = false) => {
          console.log(
            "[UnifiedDataStore] fetchWorkoutLogs called with forceRefresh:",
            forceRefresh,
          );

          // Check authentication status before attempting to fetch data
          const authStatus = checkAuthenticationStatus();
          console.log("[UnifiedDataStore] Auth status:", authStatus);

          if (!authStatus.isAuthenticated || !authStatus.hasToken) {
            const errorMessage =
              authStatus.authError ||
              "Cannot fetch workout logs: User not authenticated or missing token";
            console.warn(
              "UnifiedDataStore: Skipping workout logs fetch -",
              errorMessage,
            );

            set((state) => {
              state.errors.workoutLogs = errorMessage;
              state.loading.workoutLogs = false;
              state.dataFetched.workoutLogs = false;
            });
            return;
          }

          if (
            !forceRefresh &&
            get().dataFetched.workoutLogs &&
            navigator.onLine
          ) {
            console.log("[UnifiedDataStore] Data already fetched, skipping");
            return;
          }

          const isOnline = typeof navigator !== "undefined" && navigator.onLine;
          console.log("[UnifiedDataStore] Online status:", isOnline);

          if (!isOnline && !forceRefresh) {
            if (get().workoutLogs.length > 0) {
              console.log("Offline, using cached workout logs data.");
              set((state) => {
                state.loading.workoutLogs = false;
                state.dataFetched.workoutLogs = true;
              });
              return;
            }
            set((state) => {
              state.errors.workoutLogs = "Offline and no data cached.";
              state.loading.workoutLogs = false;
              state.dataFetched.workoutLogs = false;
            });
            return;
          }

          if (forceRefresh) {
            console.log(
              "[UnifiedDataStore] Force refreshing workout logs data...",
            );
            clearApiCache();
            localStorage.removeItem("unified-workout-storage");
          }

          console.log("[UnifiedDataStore] Starting workout logs fetch...");
          set((state) => {
            state.loading.workoutLogs = true;
            state.errors.workoutLogs = null;
          });

          try {
            // Use direct spreadsheet API call with the correct sheet name
            console.log(
              "[UnifiedDataStore] Calling readSpreadsheetData for Workout_Log...",
            );
            const result = await readSpreadsheetData("Workout_Logs");
            console.log(
              "[UnifiedDataStore] readSpreadsheetData result:",
              result,
            );

            if (result.success && result.data) {
              console.log(
                "[UnifiedDataStore] Processing",
                result.data.length,
                "workout log entries",
              );

              // Convert the generic data to WorkoutLogEntry format with proper field mapping for underscore column names
              const logsWithClientFields = result.data.map(
                (row: GenericSheetRowData) => ({
                  id: (row.ID || row.id || generateId()) as string,
                  date: (row.Date || row.date || "") as string,
                  muscleGroup: (row.Muscle_Group ||
                    row["Muscle Group"] ||
                    row.muscleGroup ||
                    "") as string,
                  exercise: (row.Exercise || row.exercise || "") as string,
                  sets: parseInt(String(row.Sets || row.sets || "1")),
                  reps: parseInt(String(row.Reps || row.reps || "0")),
                  weight: parseFloat(String(row.Weight || row.weight || "0")),
                  rating: parseInt(String(row.Rating || row.rating || "0")),
                  restTime:
                    parseInt(
                      String(
                        row.Rest_Time ||
                          row["Rest Time"] ||
                          row.restTime ||
                          "0",
                      ),
                    ) || null,
                  isSynced: true,
                  pendingAction: null,
                }),
              ) as OldWorkoutLogEntry[];

              console.log(
                "[UnifiedDataStore] Processed logs sample:",
                logsWithClientFields.slice(0, 3),
              );

              set((state) => {
                state.workoutLogs = logsWithClientFields;
                state.loading.workoutLogs = false;
                state.dataFetched.workoutLogs = true;
                state.errors.workoutLogs = null;
              });

              // Compute daily metrics after successfully loading workout logs
              console.log("[UnifiedDataStore] Computing daily metrics...");
              get().computeDailyMetricsData();
              console.log("[UnifiedDataStore] Daily metrics computed");
            } else {
              console.error(
                "Failed to fetch workout logs from sheet:",
                result.error,
              );
              set((state) => {
                state.errors.workoutLogs =
                  result.error || "Failed to fetch workout logs.";
                state.loading.workoutLogs = false;
                state.dataFetched.workoutLogs = false;
              });
            }
          } catch (err) {
            const error = err as Error;
            console.error("Error in fetchWorkoutLogs:", error);
            set((state) => {
              state.errors.workoutLogs = error.message;
              state.loading.workoutLogs = false;
              state.dataFetched.workoutLogs = false;
            });
          }
        },

        addWorkoutLog: async (newLogData: Omit<OldWorkoutLogEntry, "id">) => {
          const localId = generateId();
          const entryWithLocalId = {
            ...newLogData,
            id: localId,
            createdAt: new Date().toISOString(),
            updatedAt: new Date().toISOString(),
            isSynced: false,
          } as OldWorkoutLogEntry & { isSynced: boolean };

          set((state) => {
            state.workoutLogs = [entryWithLocalId, ...state.workoutLogs];
            state.loading.workoutLogs = navigator.onLine;
            state.errors.workoutLogs = null;
          });

          // Update computed metrics
          get().computeDailyMetricsData();

          if (!navigator.onLine) {
            console.warn(
              "Offline: Workout log added to local cache. Will sync when online.",
            );
            return entryWithLocalId;
          }

          try {
            const sheetName = "Workout_Logs";
            const sheetRow: GenericSheetRowData = {
              date: newLogData.date,
              muscleGroup: newLogData.muscleGroup,
              exercise: newLogData.exercise,
              reps: newLogData.reps,
              weight: newLogData.weight,
              rating: newLogData.rating,
              restTime: newLogData.restTime ?? "",
            };

            const response = await createSpreadsheetRow(sheetName, sheetRow);

            if (response.success) {
              set((state) => {
                const logIndex = state.workoutLogs.findIndex(
                  (log) => log.id === localId,
                );
                if (logIndex !== -1) {
                  state.workoutLogs[logIndex].isSynced = true;
                  // Handle rowIndex safely since response may not have rowId
                  if ("rowId" in response && response.rowId) {
                    const rowIndex = parseInt(
                      response.rowId.split("!").pop() || "0",
                    );
                    if (!isNaN(rowIndex)) {
                      state.workoutLogs[logIndex].rowIndex = rowIndex;
                    }
                  }
                }
                state.loading.workoutLogs = false;
              });
              return entryWithLocalId;
            } else {
              set((state) => {
                state.errors.workoutLogs =
                  response.error || "Failed to sync workout log.";
                state.loading.workoutLogs = false;
              });
              return entryWithLocalId;
            }
          } catch (err) {
            const error = err as Error;
            console.error("Error adding workout log:", error);
            set((state) => {
              state.errors.workoutLogs = error.message;
              state.loading.workoutLogs = false;
            });
            return entryWithLocalId;
          }
        },

        editWorkoutLog: async (updatedLog: OldWorkoutLogEntry) => {
          set((state) => {
            const logIndex = state.workoutLogs.findIndex(
              (log) => log.id === updatedLog.id,
            );
            if (logIndex !== -1) {
              state.workoutLogs[logIndex] = {
                ...updatedLog,
                updatedAt: new Date().toISOString(),
                isSynced: false,
              } as OldWorkoutLogEntry & { isSynced: boolean };
            }
            state.loading.workoutLogs = navigator.onLine;
            state.errors.workoutLogs = null;
          });

          // Update computed metrics
          get().computeDailyMetricsData();

          if (!navigator.onLine) {
            console.warn(
              "Offline: Workout log edit saved locally. Will sync when online.",
            );
            return;
          }

          try {
            const sheetName = "Workout_Logs";
            const sheetRow: GenericSheetRowData = {
              date: updatedLog.date,
              muscleGroup: updatedLog.muscleGroup,
              exercise: updatedLog.exercise,
              reps: updatedLog.reps,
              weight: updatedLog.weight,
              rating: updatedLog.rating,
              restTime: updatedLog.restTime ?? "",
            };

            const response = await updateSpreadsheetRow(
              sheetName,
              updatedLog.rowIndex || 0,
              sheetRow,
            );

            if (response.success) {
              set((state) => {
                const logIndex = state.workoutLogs.findIndex(
                  (log) => log.id === updatedLog.id,
                );
                if (logIndex !== -1) {
                  state.workoutLogs[logIndex].isSynced = true;
                }
                state.loading.workoutLogs = false;
              });
            } else {
              set((state) => {
                state.errors.workoutLogs =
                  response.error || "Failed to sync workout log edit.";
                state.loading.workoutLogs = false;
              });
            }
          } catch (err) {
            const error = err as Error;
            console.error("Error editing workout log:", error);
            set((state) => {
              state.errors.workoutLogs = error.message;
              state.loading.workoutLogs = false;
            });
          }
        },

        removeWorkoutLog: async (logId: string) => {
          const logToRemove = get().workoutLogs.find((log) => log.id === logId);

          set((state) => {
            state.workoutLogs = state.workoutLogs.filter(
              (log) => log.id !== logId,
            );
            state.loading.workoutLogs = navigator.onLine;
            state.errors.workoutLogs = null;
          });

          if (!navigator.onLine) {
            console.warn(
              "Offline: Workout log removal saved locally. Will sync when online.",
            );
            return;
          }

          if (!logToRemove || !logToRemove.rowIndex) {
            set((state) => {
              state.loading.workoutLogs = false;
            });
            return;
          }

          try {
            const sheetName = "Workout_Logs";
            const response = await deleteSpreadsheetRow(
              sheetName,
              logToRemove.rowIndex,
            );

            if (response.success) {
              set((state) => {
                state.loading.workoutLogs = false;
              });
            } else {
              // If deletion failed, restore the log
              set((state) => {
                state.workoutLogs = [logToRemove, ...state.workoutLogs];
                state.errors.workoutLogs =
                  response.error || "Failed to sync workout log removal.";
                state.loading.workoutLogs = false;
              });
            }
          } catch (err) {
            const error = err as Error;
            console.error("Error removing workout log:", error);
            // Restore the log on error
            set((state) => {
              state.workoutLogs = [logToRemove, ...state.workoutLogs];
              state.errors.workoutLogs = error.message;
              state.loading.workoutLogs = false;
            });
          }
        },

        syncPendingLogs: async () => {
          const unsyncedLogs = get().workoutLogs.filter((log) => !log.isSynced);

          if (unsyncedLogs.length === 0) {
            return;
          }

          set((state) => {
            state.loading.workoutLogs = true;
            state.errors.workoutLogs = null;
          });

          try {
            for (const log of unsyncedLogs) {
              // Implementation would depend on whether it's a new log, edit, or deletion
              // This is a simplified version
              const sheetName = "Workout_Logs";
              const sheetRow: GenericSheetRowData = {
                date: log.date,
                muscleGroup: log.muscleGroup,
                exercise: log.exercise,
                reps: log.reps,
                weight: log.weight,
                rating: log.rating,
                restTime: log.restTime ?? "",
              };

              let response;
              if (log.rowIndex) {
                response = await updateSpreadsheetRow(
                  sheetName,
                  log.rowIndex,
                  sheetRow,
                );
              } else {
                response = await createSpreadsheetRow(sheetName, sheetRow);
              }
              if (response.success) {
                set((state) => {
                  const logIndex = state.workoutLogs.findIndex(
                    (l) => l.id === log.id,
                  );
                  if (logIndex !== -1) {
                    state.workoutLogs[logIndex].isSynced = true; // Handle rowId safely since response may not have rowId
                    if (
                      "rowId" in response &&
                      typeof response.rowId === "string"
                    ) {
                      const rowIndex = parseInt(
                        response.rowId.split("!").pop() || "0",
                      );
                      if (!isNaN(rowIndex)) {
                        state.workoutLogs[logIndex].rowIndex = rowIndex;
                      }
                    }
                  }
                });
              }
            }

            set((state) => {
              state.loading.workoutLogs = false;
            });
          } catch (err) {
            const error = err as Error;
            console.error("Error syncing pending logs:", error);
            set((state) => {
              state.errors.workoutLogs = error.message;
              state.loading.workoutLogs = false;
            });
          }
        },

        getPreviousHighestVolumeWorkout: (
          exercise: string,
          muscleGroup: string,
        ) => {
          const { workoutLogs } = get();

          // Filter logs by exercise and muscle group
          const relevantLogs = workoutLogs.filter(
            (log) =>
              log.exercise === exercise && log.muscleGroup === muscleGroup,
          );

          if (relevantLogs.length === 0) {
            return null;
          }

          // Group by date and calculate volume
          const volumeByDate: Record<
            string,
            { totalVolume: number; logs: OldWorkoutLogEntry[] }
          > = relevantLogs.reduce(
            (acc, log) => {
              const date = log.date;
              const volume = log.weight * log.reps;
              if (!acc[date]) {
                acc[date] = { totalVolume: 0, logs: [] };
              }
              acc[date].totalVolume += volume;
              acc[date].logs.push(log);
              return acc;
            },
            {} as Record<
              string,
              { totalVolume: number; logs: OldWorkoutLogEntry[] }
            >,
          );

          // Find the date with highest volume
          let maxVolume = 0;
          let highestVolumeDate = "";
          let highestVolumeLogs: OldWorkoutLogEntry[] = [];

          for (const date in volumeByDate) {
            if (volumeByDate[date].totalVolume > maxVolume) {
              maxVolume = volumeByDate[date].totalVolume;
              highestVolumeDate = date;
              highestVolumeLogs = volumeByDate[date].logs;
            }
          }

          if (highestVolumeLogs.length === 0) {
            return null;
          }

          // Calculate aggregate stats for the highest volume date
          const totalReps = highestVolumeLogs.reduce(
            (sum: number, log: OldWorkoutLogEntry) => sum + log.reps,
            0,
          );
          const maxWeight = Math.max(
            ...highestVolumeLogs.map((log: OldWorkoutLogEntry) => log.weight),
          );
          const totalSets = highestVolumeLogs.length;

          // Return the structured response
          return {
            date: highestVolumeDate,
            totalVolume: maxVolume,
            maxWeight,
            totalReps,
            weight: maxWeight,
            reps: Math.max(
              ...highestVolumeLogs.map((log: OldWorkoutLogEntry) => log.reps),
            ),
            highestVolumeDate,
            totalSets,
            allSets: highestVolumeLogs.map((log: OldWorkoutLogEntry) => ({
              id: log.id,
              weight: log.weight,
              reps: log.reps,
              rating: log.rating,
              restTime: null,
              volume: log.weight * log.reps,
            })),
            highestVolume: {
              date: highestVolumeDate,
              totalVolume: maxVolume,
              maxWeight,
              totalReps,
              weight: maxWeight,
              reps: Math.max(
                ...highestVolumeLogs.map((log: OldWorkoutLogEntry) => log.reps),
              ),
            },
            progressionTargets: {
              targetVolume: maxVolume * 1.05,
              progressionRate: 5,
              sameWeightMoreReps: {
                weight: maxWeight,
                reps:
                  Math.max(
                    ...highestVolumeLogs.map(
                      (log: OldWorkoutLogEntry) => log.reps,
                    ),
                  ) + 1,
                additionalReps: 1,
                sets: totalSets,
              },
              moreSets: {
                weight: maxWeight,
                reps: Math.max(
                  ...highestVolumeLogs.map(
                    (log: OldWorkoutLogEntry) => log.reps,
                  ),
                ),
                additionalSets: 1,
                sets: totalSets + 1,
              },
              balancedProgression: {
                weight: maxWeight,
                reps:
                  Math.max(
                    ...highestVolumeLogs.map(
                      (log: OldWorkoutLogEntry) => log.reps,
                    ),
                  ) + 1,
                additionalReps: 1,
                sets: totalSets,
                additionalSets: 0,
              },
            },
          };
        },

        // =============================================================================
        // WORKOUT TEMPLATE ACTIONS
        // =============================================================================
        fetchTemplates: async (forceRefresh = false) => {
          // Check authentication status before attempting to fetch data
          const authStatus = checkAuthenticationStatus();

          if (!authStatus.isAuthenticated || !authStatus.hasToken) {
            const errorMessage =
              authStatus.authError ||
              "Cannot fetch templates: User not authenticated or missing token";
            console.warn(
              "UnifiedDataStore: Skipping templates fetch -",
              errorMessage,
            );

            set((state) => {
              state.errors.templates = errorMessage;
              state.loading.templates = false;
            });
            return;
          }

          const isOnline = typeof navigator !== "undefined" && navigator.onLine;
          if (!isOnline && !forceRefresh) {
            console.log("Offline, using cached workout template data.");
            set((state) => {
              state.loading.templates = false;
              state.errors.templates = null;
            });
            return;
          }

          set((state) => {
            state.loading.templates = true;
            state.errors.templates = null;
          });
          try {
            // Use optimized data fetcher for better performance
            const result = await readSpreadsheetData("WorkoutTemplates");

            if (result.success && result.data) {
              // Convert raw sheet data to WorkoutTemplate format
              const templatesData = result.data.map(
                (row: GenericSheetRowData) => ({
                  id: (row.ID || row.id || generateId()) as string,
                  name: (row.Name || row.name || "") as string,
                  description: (row.Description ||
                    row.description ||
                    "") as string,
                  exercises: JSON.parse(
                    String(row.Exercises || row.exercises || "[]"),
                  ) as WorkoutTemplateType["exercises"],
                  createdAt: (row.Created_At ||
                    row.CreatedAt ||
                    row.createdAt ||
                    new Date().toISOString()) as string,
                  lastUsed: (row.Last_Used ||
                    row.LastUsed ||
                    row.lastUsed ||
                    "") as string,
                }),
              ) as WorkoutTemplateType[];

              set((state) => {
                state.templates = templatesData;
                state.loading.templates = false;
                state.dataFetched.templates = true;
                state.errors.templates = null;
              });
            } else {
              set((state) => {
                state.errors.templates =
                  result.error || "Failed to fetch workout templates.";
                state.loading.templates = false;
              });
            }
          } catch (err) {
            console.error("Error fetching workout templates:", err);
            set((state) => {
              state.errors.templates = (err as Error).message;
              state.loading.templates = false;
            });
          }
        },

        saveTemplate: async (template: WorkoutTemplateType) => {
          try {
            const sheetName = "WorkoutTemplates";
            const rowData = {
              Name: template.name,
              Description: template.description,
              Exercises: JSON.stringify(template.exercises),
              CreatedAt: template.createdAt || new Date().toISOString(),
              LastUsed: template.lastUsed || "",
            };

            const response = await createSpreadsheetRow(sheetName, rowData);

            if (response.success) {
              set((state) => {
                const existingIndex = state.templates.findIndex(
                  (t) => t.name === template.name,
                );
                if (existingIndex !== -1) {
                  state.templates[existingIndex] = template;
                } else {
                  state.templates.push(template);
                }
              });
              return { success: true };
            } else {
              return {
                success: false,
                error: response.error || "Failed to save template.",
              };
            }
          } catch (err) {
            const error = err as Error;
            console.error("Error saving workout template:", error);
            return { success: false, error: error.message };
          }
        },
        deleteTemplate: async (templateId: string) => {
          try {
            // Implementation would need to find the row index and delete
            // For now, just remove from local state
            set((state) => {
              state.templates = state.templates.filter(
                (t) => t.id !== templateId,
              );
            });
            return { success: true };
          } catch (err) {
            const error = err as Error;
            console.error("Error deleting workout template:", error);
            return { success: false, error: error.message };
          }
        },

        getTemplateByName: (name: string) => {
          return get().templates.find((t) => t.name === name);
        },

        getTemplateById: (id: string) => {
          return get().templates.find((t) => t.id === id);
        },

        updateTemplate: (template: WorkoutTemplateType) => {
          set((state) => {
            const index = state.templates.findIndex(
              (t) => t.name === template.name,
            );
            if (index !== -1) {
              state.templates[index] = template;
            }
          });
        },

        // Active workout functions
        startWorkout: async (templateName: string) => {
          const template = get().getTemplateByName(templateName);
          if (!template) {
            return { success: false, error: "Template not found" };
          }
          const activeWorkout: ActiveWorkout = {
            id: generateId(),
            templateName: template.name,
            description: template.description,
            startTime: new Date().toISOString(),
            lastUpdated: new Date().toISOString(),
            exercises: template.exercises.map((ex) => ({
              ...ex,
              setsCompleted: 0,
              percentComplete: 0,
            })),
            isCompleted: false,
            duration: 0,
            notes: "",
          };

          set((state) => {
            state.activeWorkout = activeWorkout;
          });

          return { success: true };
        },

        completeWorkout: async () => {
          const activeWorkout = get().activeWorkout;
          if (!activeWorkout) {
            return { success: false, error: "No active workout" };
          } // Calculate duration and mark as completed
          const endTime = new Date();
          const startTime = new Date(activeWorkout.startTime);
          const duration = Math.floor(
            (endTime.getTime() - startTime.getTime()) / (1000 * 60),
          ); // duration in minutes

          set((state) => {
            if (state.activeWorkout) {
              state.activeWorkout.isCompleted = true;
              state.activeWorkout.duration = duration;
              state.activeWorkout.lastUpdated = endTime.toISOString();
            }
          });

          // Update template last used date
          const template = get().getTemplateByName(activeWorkout.templateName);
          if (template) {
            const updatedTemplate = {
              ...template,
              lastUsed: new Date().toISOString(),
            };
            get().updateTemplate(updatedTemplate);
          }

          // Clear active workout after a delay or immediately
          setTimeout(() => {
            set((state) => {
              state.activeWorkout = null;
            });
          }, 1000);

          return { success: true };
        },

        updateActiveWorkout: (workout: Partial<ActiveWorkout>) => {
          set((state) => {
            if (state.activeWorkout) {
              Object.assign(state.activeWorkout, workout);
            }
          });
        },
        updateExerciseProgress: (
          exerciseIndex: number,
          setsCompleted: number,
        ) => {
          set((state) => {
            if (
              state.activeWorkout &&
              state.activeWorkout.exercises[exerciseIndex]
            ) {
              state.activeWorkout.exercises[exerciseIndex].setsCompleted =
                setsCompleted;
              // Calculate completion percentage
              const targetSets =
                state.activeWorkout.exercises[exerciseIndex].sets || 1;
              state.activeWorkout.exercises[exerciseIndex].percentComplete =
                Math.min(100, (setsCompleted / targetSets) * 100);
            }
          });
        },

        addExerciseSet: (exerciseIndex: number) => {
          set((state) => {
            if (
              state.activeWorkout &&
              state.activeWorkout.exercises[exerciseIndex]
            ) {
              const exercise = state.activeWorkout.exercises[exerciseIndex];
              exercise.sets = (exercise.sets || 1) + 1;
            }
          });
        },

        cancelWorkout: () => {
          set((state) => {
            state.activeWorkout = null;
          });
        },

        getActiveWorkout: () => {
          return get().activeWorkout;
        },

        // =============================================================================
        // DASHBOARD LAYOUT ACTIONS
        // =============================================================================

        setDashboardLayouts: (layouts: { [key: string]: Layout[] }) => {
          set((state) => {
            state.dashboardLayouts = layouts;
          });
        },

        resetDashboardLayouts: () => {
          set((state) => {
            state.dashboardLayouts = defaultDashboardLayouts;
          });
        },

        toggleDashboardEditMode: () => {
          set((state) => {
            state.isDashboardEditMode = !state.isDashboardEditMode;
          });
        },

        setDashboardEditMode: (isEditMode: boolean) => {
          set((state) => {
            state.isDashboardEditMode = isEditMode;
          });
        },

        setDashboardCustomizable: (customizable: boolean) => {
          set((state) => {
            state.isDashboardCustomizable = customizable;
          });
        },

        // =============================================================================
        // GENERAL ACTIONS
        // =============================================================================

        clearAllData: () => {
          set((state) => {
            state.exercises = [];
            state.exerciseGroups = [];
            state.workoutLogs = [];
            state.dailyMetrics = [];
            state.templates = [];
            state.activeWorkout = null;
            state.dashboardLayouts = defaultDashboardLayouts;
            state.isDashboardEditMode = false;

            state.loading = {
              isLoading: false,
              exercises: false,
              workoutLogs: false,
              templates: false,
              general: false,
            };

            state.errors = {
              exercises: null,
              workoutLogs: null,
              templates: null,
              general: null,
            };

            state.dataFetched = {
              exercises: false,
              workoutLogs: false,
              templates: false,
            };
          });
        },

        clearError: (errorKey: keyof ErrorState) => {
          set((state) => {
            state.errors[errorKey] = null;
          });
        },
        fetchAllData: async (forceRefresh = false) => {
          console.log(
            "[UnifiedDataStore] fetchAllData called with forceRefresh:",
            forceRefresh,
          );

          set((state) => {
            state.loading.general = true;
            state.errors.general = null;
          });

          try {
            // Fetch each data type individually
            console.log("[UnifiedDataStore] Fetching exercises...");
            await get().fetchExercises(forceRefresh);

            console.log("[UnifiedDataStore] Fetching workout logs...");
            await get().fetchWorkoutLogs(forceRefresh);

            console.log("[UnifiedDataStore] Fetching templates...");
            await get().fetchTemplates(forceRefresh);

            console.log("[UnifiedDataStore] All data fetched successfully");
            set((state) => {
              state.loading.general = false;
            });
          } catch (error) {
            console.error("[UnifiedDataStore] Error in fetchAllData:", error);
            set((state) => {
              state.errors.general = (error as Error).message;
              state.loading.general = false;
            });
          }
        },

        // Computed metrics
        computeDailyMetricsData: () => {
          console.log("[UnifiedDataStore] computeDailyMetricsData called");
          const workoutLogs = get().workoutLogs;

          console.log(
            "[UnifiedDataStore] Computing metrics for",
            workoutLogs.length,
            "workout logs",
          );

          if (workoutLogs.length === 0) {
            console.log(
              "[UnifiedDataStore] No workout logs available for metrics computation",
            );
            set((state) => {
              state.dailyMetrics = [];
            });
            return;
          }

          try {
            // Use the existing computeDailyMetrics function that expects the format it was designed for
            const logsForComputation = workoutLogs.map((log) => [
              log.date,
              log.muscleGroup,
              log.exercise,
              log.reps,
              log.weight,
              log.rating,
              log.restTime,
            ]);

            console.log(
              "[UnifiedDataStore] Sample log for computation:",
              logsForComputation[0],
            );

            const computedMetrics = computeDailyMetrics(logsForComputation);
            console.log(
              "[UnifiedDataStore] Computed",
              computedMetrics.length,
              "daily metrics",
            );
            console.log(
              "[UnifiedDataStore] Sample computed metric:",
              computedMetrics[0],
            );

            // Convert computed metrics to DailyMetrics format
            const dailyMetricsData = computedMetrics.map((metric) => ({
              id: String(metric.id),
              date: metric.date,
              muscleGroup: metric.muscleGroup,
              exercise: metric.exercise,
              totalVolume: parseFloat(metric.totalVolume),
              totalSets: metric.totalSets,
              totalReps: parseFloat(metric.totalReps),
              averageReps: parseFloat(metric.averageReps),
              averageWeight: parseFloat(metric.averageWeight),
              averageFatigue: parseFloat(metric.averageFatigue),
              maxWeight: metric.maxWeight,
              intensity: parseFloat(metric.intensity),
              fatigue: parseFloat(metric.fatigue),
              howIFeel: metric.howIFeel,
              progressionRate:
                metric.progressionRate === "N/A" || !metric.progressionRate
                  ? "N/A"
                  : parseFloat(metric.progressionRate),
            }));

            console.log(
              "[UnifiedDataStore] Final daily metrics sample:",
              dailyMetricsData[0],
            );

            set((state) => {
              state.dailyMetrics = dailyMetricsData;
            });

            console.log("[UnifiedDataStore] Daily metrics updated in store");
          } catch (error) {
            console.error(
              "[UnifiedDataStore] Error computing daily metrics:",
              error,
            );
          }
        },

        // Grouped actions for easy component access
        workoutLogActions: {
          fetchAll: () => get().fetchWorkoutLogs(),
          create: (data) => get().addWorkoutLog(data),
          update: (updatedLog) => get().editWorkoutLog(updatedLog),
          delete: (logId) => get().removeWorkoutLog(logId),
        },

        // Add a test function to manually trigger data fetch for debugging
        testDataFetch: async () => {
          console.log("[UnifiedDataStore] Manual test data fetch triggered");
          try {
            await get().fetchWorkoutLogs(true);
            console.log(
              "[UnifiedDataStore] Test fetch completed. Current state:",
            );
            console.log("- Workout logs:", get().workoutLogs.length);
            console.log("- Daily metrics:", get().dailyMetrics.length);
            console.log("- Loading state:", get().loading);
            console.log("- Errors:", get().errors);
          } catch (error) {
            console.error("[UnifiedDataStore] Test fetch failed:", error);
          }
        },
      })),
      {
        name: "unified-workout-storage",
        version: 1,
      },
    ),
  ),
);

// Make testDataFetch available globally for debugging
if (typeof window !== "undefined") {
  (
    window as Window &
      typeof globalThis & {
        testDataFetch: () => Promise<void>;
        getUnifiedStoreState: () => UnifiedDataState;
      }
  ).testDataFetch = () => unifiedDataStore.getState().testDataFetch();
  (
    window as Window &
      typeof globalThis & {
        testDataFetch: () => Promise<void>;
        getUnifiedStoreState: () => UnifiedDataState;
      }
  ).getUnifiedStoreState = () => unifiedDataStore.getState();
}

