export const useWorkoutTemplates = () => {
  // Split into individual selectors to prevent object recreation
  const templates = useUnifiedDataStore((state) => state.templates);
  const activeWorkout = useUnifiedDataStore((state) => state.activeWorkout);
  const loading = useUnifiedDataStore((state) => state.loading.templates);
  const error = useUnifiedDataStore((state) => state.errors.templates);
  const dataFetched = useUnifiedDataStore(
    (state) => state.dataFetched.templates,
  );
  const fetchTemplates = useUnifiedDataStore((state) => state.fetchTemplates);
  const saveTemplate = useUnifiedDataStore((state) => state.saveTemplate);
  const deleteTemplate = useUnifiedDataStore((state) => state.deleteTemplate);
  const getTemplateByName = useUnifiedDataStore(
    (state) => state.getTemplateByName,
  );
  const updateTemplate = useUnifiedDataStore((state) => state.updateTemplate);
  const startWorkout = useUnifiedDataStore((state) => state.startWorkout);
  const completeWorkout = useUnifiedDataStore((state) => state.completeWorkout);
  const updateActiveWorkout = useUnifiedDataStore(
    (state) => state.updateActiveWorkout,
  );
  const updateExerciseProgress = useUnifiedDataStore(
    (state) => state.updateExerciseProgress,
  );
  const addExerciseSet = useUnifiedDataStore((state) => state.addExerciseSet);
  const cancelWorkout = useUnifiedDataStore((state) => state.cancelWorkout);
  const getActiveWorkout = useUnifiedDataStore(
    (state) => state.getActiveWorkout,
  );

  return {
    templates,
    activeWorkout,
    loading,
    error,
    dataFetched,
    fetchTemplates,
    saveTemplate,
    deleteTemplate,
    getTemplateByName,
    updateTemplate,
    startWorkout,
    completeWorkout,
    updateActiveWorkout,
    updateExerciseProgress,
    addExerciseSet,
    cancelWorkout,
    getActiveWorkout,
  };
};

export const useDashboardLayout = () => {
  // Split into individual selectors to prevent object recreation
  const layouts = useUnifiedDataStore((state) => state.dashboardLayouts);
  const isEditMode = useUnifiedDataStore((state) => state.isDashboardEditMode);
  const isCustomizable = useUnifiedDataStore(
    (state) => state.isDashboardCustomizable,
  );
  const setLayouts = useUnifiedDataStore((state) => state.setDashboardLayouts);
  const resetLayouts = useUnifiedDataStore(
    (state) => state.resetDashboardLayouts,
  );
  const toggleEditMode = useUnifiedDataStore(
    (state) => state.toggleDashboardEditMode,
  );
  const setEditMode = useUnifiedDataStore(
    (state) => state.setDashboardEditMode,
  );
  const setCustomizable = useUnifiedDataStore(
    (state) => state.setDashboardCustomizable,
  );

  return {
    layouts,
    isEditMode,
    isCustomizable,
    setLayouts,
    resetLayouts,
    toggleEditMode,
    setEditMode,
    setCustomizable,
  };
};
