const SPREADSHEET_ID = googleConfig.sheets.spreadsheetId;

async function getSheetsService(): Promise<any> {
  // Return type any for gapi.client.sheets
  if (!gapi) {
    console.error("Google API (gapi) not loaded.");
    throw new Error("Google API (gapi) not loaded.");
  }

  const { isGoogleApiInitialized, initializeGoogleApi } = await import(
    "./googleApiHelper"
  );

  let retries = 0;
  const maxRetries = 3;
  while (!isGoogleApiInitialized() && retries < maxRetries) {
    console.log(
      `Google API not initialized, attempting initialization (attempt ${retries + 1}/${maxRetries})...`,
    );
    const initialized = await initializeGoogleApi({
      retries: 2,
      timeout: 20000,
    });
    if (!initialized) {
      retries++;
      if (retries === maxRetries) {
        console.error(
          "Failed to initialize Google API after maximum attempts.",
        );
        throw new Error(
          "Failed to initialize Google API after multiple attempts.",
        );
      }
      const delay = Math.min(1000 * Math.pow(2, retries), 5000);
      await new Promise((resolve) => setTimeout(resolve, delay));
    }
  }

  if (!gapi.auth2) {
    console.error(
      "Google Auth (gapi.auth2) not initialized after API initialization.",
    );
    throw new Error("Google Auth (gapi.auth2) not initialized.");
  }

  let authInstance;
  try {
    authInstance = gapi.auth2.getAuthInstance();
  } catch (error) {
    console.error("Error getting auth instance:", error);
    throw new Error("Google Auth instance not available.");
  }

  if (!authInstance) {
    console.error("Google Auth instance not available.");
    throw new Error("Google Auth instance not available.");
  }

  if (!authInstance.isSignedIn.get()) {
    console.error("User is not signed in via Google Auth.");
    throw new Error("User not authenticated for Google Sheets API.");
  }

  if (!gapi.client || typeof gapi.client.load !== "function") {
    await new Promise<void>((resolve, reject) => {
      gapi.load("client", (err?: any) => {
        if (err || !gapi.client) {
          console.error("Failed to load gapi.client:", err);
          reject(err || new Error("Failed to load gapi.client"));
        } else {
          resolve();
        }
      });
    });
  }

  if (!gapi.client.sheets) {
    try {
      await new Promise<void>((resolve, reject) => {
        gapi.client.load("sheets", "v4").then(
          () => {
            if (gapi.client.sheets) {
              resolve();
            } else {
              console.error(
                "gapi.client.sheets not available after load call.",
              );
              reject(
                new Error("gapi.client.sheets not available after load call."),
              );
            }
          },
          (err: any) => {
            console.error("Error in gapi.client.load('sheets', 'v4'):", err);
            reject(err);
          },
        );
      });
    } catch (error) {
      console.error(
        "Error loading Google Sheets API (wrapped promise):",
        error,
      );
      throw new Error("Failed to load Google Sheets API.");
    }
  }

  if (!gapi.client.sheets) {
    console.error(
      "gapi.client.sheets is critically unavailable after loading attempts.",
    );
    throw new Error(
      "Google Sheets API (gapi.client.sheets) is critically unavailable.",
    );
  }

  return gapi.client.sheets;
}

export type SheetSpecificData =
  | WorkoutLogEntry
  | BodyWeightEntry
  | Exercise
  | WorkoutTemplate
  | WorkoutSession;

export type GenericSheetRowData = Record<string, unknown>;

export type RowIdentifier =
  | number
  | Partial<SheetSpecificData>
  | GenericSheetRowData;

export type KnownSheetName =
  | "Workout_Logs"
  | "Body_Weight"
  | "Exercises"
  | "WorkoutTemplates"
  | "Workout_Sessions"
  | "Nutrition_Log"
  | "Meal_Plans"
  | "Health_Tracking"
  | "Meditation_Log"
  | "Vital_Signs"
  | "Body_Measurements"
  | "Sleep_Log"
  | "ToDo";

