// Meal Planning API functions
export async function createMealPlan(mealPlanData: {
  date: string;
  meal: string;
  recipeName: string;
  ingredients: string;
  instructions: string;
  prepTime: number;
  cookTime: number;
  servings: number;
  calories: number;
  protein: number;
  carbs: number;
  fat: number;
  tags?: string[] | string;
  notes?: string;
}) {
  return await createSpreadsheetRow("Meal_Plans", {
    id: generateUniqueId(),
    date: mealPlanData.date,
    meal: mealPlanData.meal,
    recipeName: mealPlanData.recipeName,
    ingredients: mealPlanData.ingredients,
    instructions: mealPlanData.instructions,
    prepTime: mealPlanData.prepTime,
    cookTime: mealPlanData.cookTime,
    servings: mealPlanData.servings,
    calories: mealPlanData.calories,
    protein: mealPlanData.protein,
    carbs: mealPlanData.carbs,
    fat: mealPlanData.fat,
    tags: Array.isArray(mealPlanData.tags)
      ? mealPlanData.tags.join(",")
      : mealPlanData.tags || "",
    notes: mealPlanData.notes || "",
    timestamp: new Date().toISOString(),
  });
}

export async function getMealPlans() {
  const result = await readSpreadsheetData("Meal_Plans");
  return result.success ? result.data || [] : [];
}

export async function updateMealPlan(
  rowIndex: number,
  mealPlanData: Partial<{
    date: string;
    meal: string;
    recipeName: string;
    ingredients: string;
    instructions: string;
    prepTime: number;
    cookTime: number;
    servings: number;
    calories: number;
    protein: number;
    carbs: number;
    fat: number;
    tags: string[] | string;
    notes: string;
  }>,
) {
  const processedData = {
    ...mealPlanData,
    timestamp: new Date().toISOString(),
  } as any;

  if (mealPlanData.tags) {
    processedData.tags = Array.isArray(mealPlanData.tags)
      ? mealPlanData.tags.join(",")
      : mealPlanData.tags;
  }

  return await updateSpreadsheetRow("Meal_Plans", rowIndex, processedData);
}

export async function deleteMealPlan(rowIndex: number) {
  return await deleteSpreadsheetRow("Meal_Plans", rowIndex);
}

